.footer-ul {
  display: flex;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer-ul li {
  list-style: none;
}

.footer-ul li a {
  width: 60px;
  height: 60px;
  background-color: #fff;
  text-align: center;
  line-height: 55px;
  font-size: 25px;
  margin: 0 10px;
  display: block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
}

.footer-ul li a .i {
  position: relative;
  color: #262626;
  transition: .5s;
  z-index: 3;
}

.footer-ul li a:hover .i {
  color: #fff;
  transform: rotateY(360deg);
}

.footer-ul li a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(32, 32, 32);
  transition: .5s;
  z-index: 2;
}

.footer-ul li a:hover:before {
  top: 0;
}

.footer-ul li:nth-child(1) a:before {
  background: #3b5999;
}

.footer-ul li:nth-child(2) a:before {
  background: #55acee;
}

.footer-ul li:nth-child(3) a:before {
  background: #0077b5;
}

.footer-ul li:nth-child(4) a:before {
  background: rgb(32, 32, 32);
}